* {
    box-sizing: border-box;
}

.fwd {
    width: 7vw;
    height: 7vw;
    position: absolute;
    bottom: 0;
    right: 0;
&.puguly {
        width: 17vw;
        height: auto;
        margin-right: 2vw;
    }
}

.back {
    width: 7vw;
    height: 7vw;
    position: absolute;
    bottom: 0;
    left: 0;
}

.home {
    width: 7vw;
    height: 7vw;
    position: absolute;
    top: 0;
    right: 0;
}

.background__container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    z-index: -1;
    display: flex;
    align-items: flex-end;
    overflow: hidden;

    &.linie {
        width: auto;
        z-index: -2;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        img {
            bottom: 0;
            width: 100%;
            }
            }
}

.page__container {
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    .header__container {
        width: 100%;
        height: 27%;
        background-color: white;
        padding: 1vw;
        display: grid;
        grid-template-rows: 1fr 1fr;

        &.page08 {
            width: 90%;
            height: 25%;
            background: none;

            h1 {
                text-align: left;
                justify-self: flex-start;
                margin-left: 2vw;
            }
        }
        &.page11 {
            height: 17%;
        
        h1 {
            color: #777777;
            font-weight: 700;
            font-size: calc(25px + 1vw);
            text-align: center;
            justify-self: center;
            align-self: center;
            }
            }
            }
        .icons__container {
            width: 100%;
            height: 40%;
            display: grid;
            grid-template-columns: 1.3fr 1fr 1fr;

            &.page11 {
                    width: 90%;
                }
            img {
                width: 90%;
                align-self: center;
                justify-self: start;
                &:nth-of-type(2) {
                    justify-self: center;
                
                &:nth-of-type(3) {
                    justify-self: end;
                }
            }
        }
    }

    .grades__container {
        width: 100%;
        height: 65%;
        display: grid;
        grid-template-columns: 1fr 1fr;

        &.page11 {
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: 1fr 1fr;
        
        
                .container {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    height: 100% / 3;
        
                    .szczala {
                        padding: 2vw;
                        width: 50%;
                    }
        
                    .gira {
                        width: 25%;
                        margin-left: 3vw;
                    }
                                }
            }
        &.page08 {
            height: 60%;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
            height: 100%;
            width: auto;
            }

            h1 {
                font-size: calc(15px + 1vw);
                margin-left: 2vw;

                sup {
                    vertical-align: super;
                    font-size: 14px;
                }
            }
        }

        &.page09 {
            height: 65%;
            grid-template-columns: 1fr;
            grid-template-rows: 1.3fr 2fr;
            padding: 0 2vw;

            .container__up {
                display: grid;
                grid-template-columns: 1.5fr 1fr .2fr .5fr;

                &.page10 {
                    height: 100%;
                        grid-template-columns: 1fr 1fr 1fr;
                    
                        .container {
                            box-sizing: border-box;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            padding: 0 1vw;
                        }
                                                .title_orange {
                                                    color: #ff7900;
                                                    text-align: center;
                                                    text-transform: uppercase;
                                                    font-size: calc(20px + 1vw);
                            font-weight: 800;
                        }
                        
                        .title_black {
                            display: inline-block;
                            text-align: left;
                            color: black;
                            font-weight: 400;
                            font-size: calc(15px + 1vw);
                        margin-bottom: 5vw;
                        
                            sup {
                                vertical-align: super;
                                font-size: calc(8px + .2vw);
                            }
                    }
                }
}

            .container_down {
                height: 75%;
                display: grid;
                grid-template-columns: 1fr 2fr;
                padding: 0 2vw;

                &.page10 {
                    grid-template-columns: 1fr 1fr;
                    grid-template-rows: repeat(3, 1fr);
                    gap: 3vw;

                    h1 {
                        text-align: left;
                        font-size: calc(15px + 1vw);
                        justify-self: flex-start;

                        sup {
                            vertical-align: super;
                            font-size: 14px;
                        }
                    }
                }

                img {
                    width: 70%;
                    align-self: flex-start;
                    justify-self: center;
                }
            }

            h1 {
                font-size: calc(15px + .5vw);
                font-weight: 600;
                align-self: center;
                justify-self: center;
            }

            img {
                width: 100%;
            }
        }

        img {
            width: 60%;
            justify-self: center;
            align-self: center;
            cursor: pointer;
        }
    }
}

.notes {
    position: absolute;
    left: 2%;
    bottom: 2%;
    z-index: 999;
    font-size: calc(9px + .2vw);
    font-weight: 200;


    &.page08 {
        margin-left: 8vw;
        width: 80%;
    }
}